.imageAndText {
    height: 101px;
    width: 1210px;
    font-family: 'Montserrat', sans-serif;

}

.imageAndText img {
    position: absolute;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    margin-left: 1150px;
    margin-top: 0px;
}

.text {
    position: absolute;
    width: 527px;
    height: 60px;
    margin: 0px 118px 252px 610px;
    background: #0066CC;
    border-radius: 10px;
}

.text:before{
    content: ' ';
    position: absolute;
    width: 0;
    height: 0;
    left: 511px;
    top: 35px;
    border-bottom: 25px solid #0066CC;
    border-right: 30px solid transparent;
}

.name {
    margin-left: 462px;
    font-size: 18px;
    margin-top: 5px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-style: normal;
    margin-left: 13px;
    font-size: 16px;
    margin-top: 8px;
    line-height: 20px;
    color: white;
}

.time {
    position: absolute;
    font-size: 10px;
    color: #000000;
    margin-top: -50px;
    margin-left: 1160px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
}

